.Header {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  background-color: #fff;
  margin: 20px auto;
  background-color: #0099ff;
  border-radius: 5px;
}
.Header a {
  text-decoration: none;
  margin: auto;
  width: 25%;
  font-size: 16pt;
  text-align: center;
  color: #fff;
}
.Header a:hover,
.Header a :focus {
  background-color: #fff;
  color: #0099ff;
}
