.DataItems {
  position: relative;
  display: inline-block;
  width: 100%;
}
.DataItems .bottom-area {
  position: relative;
  width: 90%;
  margin: auto;
  bottom: 0;
  height: 50px;
}
.DataItems .bottom-area .Previous {
  position: absolute;
  left: 20px;
  margin: 5px auto;
}
.DataItems .bottom-area .Next {
  position: relative;
  float: right;
  right: 20px;
  margin: 5px auto;
}
